/* Palette generated by Material Palette - materialpalette.com/green/blue */
.cbx-toggle .styled-checkbox {
  position: absolute;
  opacity: 0; }
  .cbx-toggle .styled-checkbox + label {
    position: relative;
    cursor: pointer;
    padding: 0; }
  .cbx-toggle .styled-checkbox + label:before {
    content: "";
    margin-right: 10px;
    display: inline-block;
    vertical-align: text-top;
    width: 25px;
    height: 25px;
    background: #ffffff;
    border: 1px solid #1abc9c; }
  .cbx-toggle .styled-checkbox:hover + label:before {
    background: #1abc9c; }
  .cbx-toggle .styled-checkbox:focus + label:before {
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12); }
  .cbx-toggle .styled-checkbox:checked + label:before {
    background: #1abc9c; }
  .cbx-toggle .styled-checkbox:disabled + label {
    color: #b8b8b8;
    cursor: auto; }
  .cbx-toggle .styled-checkbox:disabled + label:before {
    box-shadow: none;
    background: #ddd; }
  .cbx-toggle .styled-checkbox:checked + label:after {
    content: "";
    position: absolute;
    left: 8px;
    top: 12px;
    background: white;
    width: 3px;
    height: 4px;
    box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white, 4px -6px 0 white, 4px -8px 0 white;
    transform: rotate(45deg); }
